import React , {Component} from 'react'
import Post from './Post';
import Answer from './Answer';


class Feeds extends Component{

    state= {
            selectedQuestion : 0,
            answerOpen : true
    }

    render(){

        var feeds = this.props.feeds; 
        return <div style={{
            maxWidth: '800px',
            verticalAlign : 'center',
            display:'inline-flex',
            marginTop : 0
        }}>

        <Answer open = {this.state.answerOpen} onClose={()=>{
            this.setState({
                answerOpen : false
            })
        }} answers={feeds[this.state.selectedQuestion]}/>

        <center>
        {
           
            feeds.map((x,i)=>{
                return <Post onClick={()=>{
                    this.setState({
                        answerOpen : true,
                        selectedQuestion : i
                    })
                }} que={
                    x.description
                } ans={x.answers[0] && x.answers[0].description} votes={

                  x.answers[0] ?  x.answers[0].votes.reduce((total,x)=>{

                            if(x.is_upvote)
                            {
                                return  total+1;
                            }


                    },0)  :0

                } ansby />
            })
        }
        </center>

        </div>;
    }
}

export default Feeds;


