import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import Button from 'material-ui/Button';
import Dialog from 'material-ui/Dialog';
import List, { ListItem, ListItemText } from 'material-ui/List';
import Divider from 'material-ui/Divider';
import AppBar from 'material-ui/AppBar';
import Toolbar from 'material-ui/Toolbar';
import IconButton from 'material-ui/IconButton';
import Typography from 'material-ui/Typography';
import CloseIcon from 'material-ui-icons/Close';
import Slide from 'material-ui/transitions/Slide';
import Post from './Post';
import TextField from 'material-ui/TextField';


const styles = {
  appBar: {
    position: 'relative',
    backgroundColor: '#222'

  },
  flex: {
    flex: 1,

  },
};

function Transition(props) {
  return <Slide direction="up" {...props} style={{

    maxWidth: '800px'
  }} />;
}

class FullScreenDialog extends React.Component {
  state = {
    open: true,
  };


  handleChange = name => event => {
    this.setState({
      [name]: event.target.value,
    });
  };

  handleClickOpen = () => {
    this.setState({ open: true });
  };

  handleClose = () => {
    this.props.onClose();
  };

  render() {
    const { classes } = this.props;
    return (
      <center style={{

        height: '100vh',
        verticalAlign: 'center',
        display: 'inline-flex'

      }}>
        {/* <Button onClick={this.handleClickOpen}>Open full-screen dialog</Button> */}
        <Dialog
          fullScreen
          open={this.props.open}
          onClose={this.handleClose}
          transition={Transition}

          style={{

            verticalAlign: 'center',
            display: 'inline-flex'
          }}
        >
          <AppBar className={classes.appBar}>
            <Toolbar>
              <IconButton color="inherit" onClick={this.handleClose} aria-label="Close">
                <CloseIcon />
              </IconButton>
              <Typography variant="title" color="inherit" className={classes.flex}>
                {this.props.answers.description}
              </Typography>
              {/* <Button color="inherit" onClick={this.handleClose}>
                save
              </Button> */}
            </Toolbar>
          </AppBar>
          <center style={{
            backgroundColor: 'rgb(37, 33, 33)',
            height: '100vh',
            marginTop: 0,
            overflowX: 'hidden'
          }}>


            {
              this.props.answers.answers.map((x, i) => {

                return <Post ans={
                  x.description
                }

                  votes={

                    x.votes.reduce((total, x) => {

                      if (x.is_upvote) {
                        return total + 1;
                      }


                    }, 0)

                  } ansby={
                    x.answeredby.name
                  } />



              })


            }

            <div>

              <TextField multiline={true} rows={25} label="Write your answer here" style={{
                width: '100%', maxWidth: '800px'
              }} onChange={this.handleChange('ans')}

              />

              <Button style={{
                width:'100%',
                color : 'white'
              }} >
              
              submit

              </Button>

            </div>



            {/* {this.props.answers.description} */}
          </center>


        </Dialog>
      </center>
    );
  }
}

FullScreenDialog.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(FullScreenDialog);


